<template>
<div>
    <div class="sign-up-form">
        <div class="back-img" v-if="userlog==null">
            <img :src="signupImg" />
            <div class="sign-up-head">
                <div></div>
            </div>
            <v-form ref="form" class="sign-up-body">
                <div>
                    <v-text-field v-model="user.id" :rules="idRule" label="* " type="id" prepend-icon="mdi-identifier"/>
                    <v-icon @click="user.id=''">mdi-delete</v-icon>
                </div>
                <div>
                    <v-text-field v-model="user.pw" :rules="pwRule" label="* " type="password" prepend-icon="mdi-lock"/>
                    <v-icon @click="user.pw=''">mdi-delete</v-icon>
                </div>
                <div>
                    <v-text-field v-model="pwcheck" :rules="pwckRule" label="* " type="password" prepend-icon="mdi-lock"/>
                    <v-icon @click="pwcheck=''">mdi-delete</v-icon>
                </div>
                <div>
                    <v-text-field v-model="user.email" :rules="emailRule" label="* E-mail" type="email" prepend-icon="mdi-email"/>
                    <v-icon @click="user.email=''">mdi-delete</v-icon>
                </div>
                <div>
                    <v-text-field v-model="user.nickname" label="" type="text" prepend-icon="mdi-account"/>
                <v-icon @click="user.nickname=''">mdi-delete</v-icon>
                </div>
                <div>
                    <v-dialog
                        ref="menu"
                        v-model="menu1"
                        :return-value.sync="user.birthday"
                        presistent
                        width="290px">
                        <template v-slot:activator="{on, attrs}">
                            <v-text-field
                                v-model="user.birthday"
                                prepend-icon="mdi-calendar"
                                readonly
                                label=""
                                v-bind="attrs"
                                v-on="on"/>
                        </template>
                        <v-date-picker v-model="user.birthday" no-title show-current="2000-07-13" scrollable :max="date">
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu1 = false">Cancel</v-btn>
                            <v-btn text color="primary" @click="writeBirth(user.birthday)">OK</v-btn>
                        </v-date-picker>
                    </v-dialog>
                    <v-icon @click="user.birthday=''">mdi-delete</v-icon>
                </div>
                <div class="sign-up-msg">*      .</div>
                <div class="sign-up-footer">
                    <v-btn depressed small dark color="blue" @click="doSignUp"><v-icon>mdi-check</v-icon></v-btn>
                    <!-- <v-btn depressed small dark><v-icon>mdi-close</v-icon></v-btn> -->
                </div>
            </v-form>
        </div>
        <div v-if="userlog!=null">
               .
        </div>
    </div>
    </div>
</template>

<script>
// import mapState from 'vuex'
import axios from 'axios'

export default{
    data(){
        return {
            
            userlog:this.$session.get("user"),
            date: (new Date()).toISOString().substr(0,10),
            signupImg:require("../assets/signupform.png"),
            user:{
                id:"",
                pw:"",
                birthday: "",
                email:"",
                nickname:"",
            },
            idRule:[
                v => !!v||' ',
                v => (v.length <=20&& v.length>4) || ' 5~20 '
            ],
            pwRule:[
                // checkpw
                v => !!v||' ',
                v => (v.length <=20&& v.length>4) || ' 5~20 '
            ],
            pwckRule:[
                v => !!v||' ',
                v => v=== this.user.pw || '  .'
            ],
            emailRule:[
                v => !!v||' ',
                v => /.+@.+\..+/.test(v) ||'  .',
                v => v.length<40 || ' 40    .'
            ],
            pwcheck:"",
            menu1: false,
            
        }
    },
    components:{
    },
    methods: {
        writeBirth(v){
            this.user.birthday = v;
            this.menu1 = false;
            this.$refs.menu.save(v);
        },
        doSignUp(){
            const config = {baseUrl: 'http://localhost:9999'};
            const validate = this.$refs.form.validate();
            if(validate){
                axios.post(`${config.baseUrl}/user/sign`,this.user)
                .then((res)=>{
                    console.log(res)
                })
            }

        }
        // checkpw(v){
        // }
    },
    setup(){
        console.log(this.$session.get("user"))
    }
}
</script>

<style scoped>
.sign-up-form{
    width: 40vw;
    min-width: 320px;
    max-width: 708px;
    height: 705px;
    margin:0 auto;
}
.back-img{
    position: absolute;
    min-width: 320px;
    height: inherit;
    width: inherit;
    max-width: 768px;
}
.back-img>img{
    width: 40vw;
    min-width: 320px;
    max-width: 708px;
    height: 705px;
    margin:0 auto;
    position: absolute;
}
.sign-up-head{
    padding: 80px 0 0 0;
    font-size: 30px;
}
.sign-up-head>div{
    margin: 0 auto;
    position: relative;
    text-align: center;
}
.sign-up-body{
    position: relative;
    padding:0 10% 0 10%;
}
.sign-up-body>div{
    display: flex;
}
.sign-up-msg{
    font-size:12px;
    color:red;
}
.sign-up-footer{
    justify-content: flex-end;
}
.sign-up-footer>button{
    margin: 0 5px;
}
@media (min-width:1800px){

    .sign-up-body{
        padding:0 5% 0 10%;
        max-width: 700px;
    }
}
</style>